import React, { useEffect, useState } from 'react';
import axios from 'axios';
import {server} from "../index.js"
import { Button, Container, Heading, HStack, Image, Text, VStack } from '@chakra-ui/react';
import Loader from './Loader.jsx';
import ErrorComponent from './ErrorComponent.jsx';



const Exchanges = () => {

  const [exchanges,setExchanges] = useState([]);
  const [loading,setloading]=useState(true);
  const [error,setError]=useState(false);
  const [page,setPage]=useState(1);


   const changePage = (page) => {
     setPage(page);
     setloading(true);
   }

   const btns = new Array(5).fill(1)

 

  useEffect(() => {

    const fetchExchanges = async()=>{
    
      try {
        const {data} = await axios.get(`${server}/exchanges?page=${page}`)
    
        setExchanges(data);
        setloading(false);
        console.log(data)
      }
      catch(error){
        setError(true);
        setloading(false);
      }

    }
   
    fetchExchanges();

  }, [page]);

  if(error) return <ErrorComponent message={"Error while Fetching Exchanges"}/>

  return <Container maxW={"container.xl"} >

      {loading? <Loader /> : ( <> 

       
      
      <HStack wrap={"wrap"} justifyContent={"space-evenly"}>
        {exchanges.map((i) => (
         <ExchangeCard 
         key={i.id} 
         name={i.name} 
         img={i.image} 
         rank={i.trust_score_rank} 
         url={i.url} 
         country={i.country}
         year={i.year_established}
         />
        ))}
      </HStack>

      <HStack w={"full"} justifyContent={"center"} overflowX={"auto"} p={"8"}>
           
         
            {
              btns.map((item,index) => (
                <Button
                key={index}
                bgColor={"blackAlpha.900"} 
                color={"white"} 
                onClick={()=> changePage(index+1)}
                
                >
                  {index + 1}
                  </Button>
              ))
            }

            
          </HStack>
          <Text
          textAlign={"center"}
          fontWeight={"bold"}
          color={"blackAlpha.800"}
          pb={"2"}
          > Designed By <a href={"https://github.com/kant146"} target={"blank"} > Kant ♥️</a></Text>

          
      
      </> )}
  </Container>
};


const ExchangeCard = ({name,img,rank,url,country,year}) => (
  <a href={url} target={"blank"}>
    
  <VStack w={"52"} shadow={"lg"} p={"8"}
   borderRadius={"lg"} 
   transition={"all 0.3s"}
   m={"4"}
   css={{
    "&:hover":{
      transform: "scale(1.1)",
    }
   }}
   >
    <Image src={img} w={"10"} height={"10"} objectFit={"contain"} alt={Exchanges} />
    <Heading size={"md"} noOfLines={1}>{rank}</Heading>
    <Text noOfLines={1}>{name}</Text>
    <Text>{country}</Text>
    <Text>{year}</Text>
  </VStack>


  </a>
)


export default Exchanges