import { Image, Text, VStack } from '@chakra-ui/react'
import React from 'react';
import {motion} from "framer-motion";

const Home = () => {
  return <VStack bgColor={"blackAlpha.900"} w={"full"} h={"85vh"} justifyContent={"space-evenly"} gap={"4"}>
<motion.div  
style={{
  height: "80vh",
}}

animate={{
  translateY:"30px",
}}

transition={{
  duration: 2,
  repeat:Infinity,
  repeatType:"reverse",
}}
>
  <a href={"https://crypt0master.vercel.app/coin/bitcoin"}>
<Image w={["80%","70%"]}
     h={"80%"}
      m={"auto"} 
      mt={"15%"}
      p={"2"}
     borderRadius={["80px","50%"]}
      border={"3px solid #F7CB13"}
       objectFit={"cover"} 
       filter={["drop-shadow(8px 8px 40px #F7CB13)","drop-shadow(8px 8px 400px #F7CB13)"]}
       src={"https://logoisus.com/wp-content/uploads/2021/09/executium-rhm7H8X5J98-unsplash_small.jpg"} 
        alt={"CryptoMaster"}
        // filter={"grayscale(11)"}
        /></a>
</motion.div  >
  <Text fontSize={["5xl","6xl"]} 
  textAlign={"center"}
  fontWeight={"extrabold"}
  color={"#F7CB13"}
  // mt={"-10"}
  >
    CryptoMaster
  </Text>

  <Text fontSize={["1.3rem","2xl"]} 
  textAlign={"center"}
  fontWeight={"bold"}
  color={"whiteAlpha.800"}
  >Get All Crypto Updates </Text>
  <Text
          textAlign={"center"}
          fontWeight={"bold"}
          color={"whiteAlpha.800"}
          pb={"2"}
          > Designed By <a href={"https://github.com/kant146"} target={"blank"} > Kant ♥️</a></Text>

  </VStack>
    
}

export default Home