import { Avatar, Box, Stack, Text, VStack} from '@chakra-ui/react'
import React from 'react'

const avatarSrc ="https://avatars.githubusercontent.com/u/47590877?v=4"

const Footer = () => {

  return <Box 
  bgColor={"blackAlpha.900"} 
  color={"whiteAlpha.700"}
  minH={"48"}
  px={"16"}
  py={["16","8"]}
  shadow={"base"}
  >
  <Stack direction={["column","row"]} h={"full"} alignItems={"center"}>
    <VStack w={"full"} alignItems={["center","flex-start"]}>
  <Text color={"whiteAlpha.400"} fontWeight={"bold"}>About Us</Text>
  <Text fontSize={"sm"} 
  color={"whiteAlpha.400"} 
  letterSpacing={"widest"}
  textAlign={["center","left"]} 
  >
    We are the best crypto trading Couch,
    we provide our guidance for free explore it well. 
  </Text>
    </VStack>
    <VStack>
      <a href={"https://github.com/kant146"} target={"blank"} >
      <Avatar filter={"drop-shadow(8px 8px 100px #F7CB13)"} boxSize={"28"} mt={["4","0"]} cursor={"pointer"} src={avatarSrc} />
      </a>
      <Text color={"whiteAlpha.400"}>Kant</Text>
    </VStack>
  </Stack>
  </Box>
}

export default Footer